import React, { useState, useEffect } from 'react';
import { Drawer, List, ListItem, ListItemText, ListItemIcon, Avatar, Box, Grid, IconButton, useMediaQuery } from '@mui/material';
import { Link } from 'react-scroll';
import HomeIcon from '@mui/icons-material/Home';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import ContactMailIcon from '@mui/icons-material/ContactMail';
import InfoIcon from '@mui/icons-material/Info';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import logo from '../assets/logo.png';

const Sidebar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [isForcedClose, setIsForcedClose] = useState(false);
  const [categories, setCategories] = useState([]);
  const isSmallScreen = useMediaQuery('(max-width: 800px)'); // 检测屏幕宽度是否小于800px

  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/data/menu_data.json`)
      .then((response) => response.json())
      .then((data) => {
        const uniqueCategories = [...new Set(data.map((item) => item.category))];
        setCategories(uniqueCategories);
      })
      .catch((error) => console.error('Error fetching menu data:', error));
  }, []);

  const handleMouseEnter = () => {
    if (!isForcedClose) setIsOpen(true);
  };

  const handleMouseLeave = () => {
    if (!isForcedClose) setIsOpen(false);
  };

  const handleToggleSidebar = () => {
    setIsOpen(false);
    setIsForcedClose(true);
    setTimeout(() => setIsForcedClose(false), 300);
  };

  const formatCategory = (category) => {
    return category.replace(/\s+/g, '_').toLowerCase();
  };

  return (
    <Box
      sx={{
        position: 'fixed',
        top: 0,
        left: 0,
        height: '100vh',
        width: isOpen ? (isSmallScreen ? '80%' : 700) : 60, // 在小屏幕时限制最大宽度为80%
        transition: 'width 0.3s ease',
        background: isOpen ? 'rgba(40, 44, 52, 0.85)' : '#282c34',
        boxShadow: isOpen ? '0px 4px 12px rgba(0, 0, 0, 0.1)' : 'none',
        padding: 0,
        margin: 0,
        overflowY: 'hidden',
        zIndex: 1200,
        whiteSpace: 'nowrap',
      }}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <Drawer
        variant="permanent"
        open={isOpen}
        sx={{
          width: isOpen ? (isSmallScreen ? '80%' : 700) : 60,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: isOpen ? (isSmallScreen ? '80%' : 700) : 60,
            boxSizing: 'border-box',
            backgroundColor: 'transparent',
            color: '#fff',
            transition: 'width 0.3s ease',
            overflowX: 'hidden',
            zIndex: 1200,
            padding: 0,
            margin: 0,
            overflowY: 'auto',
          },
        }}
      >
        {isOpen && (
          <Box
            sx={{
              position: 'absolute',
              top: 10,
              right: 10,
            }}
          >
            <IconButton onClick={handleToggleSidebar} sx={{ color: '#fff' }}>
              <ArrowBackIcon />
            </IconButton>
          </Box>
        )}
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            padding: '16px 0',
          }}
        >
          <Avatar
            src={logo}
            alt="Logo"
            sx={{
              width: isOpen ? 80 : 40,
              height: isOpen ? 80 : 40,
              transition: 'width 0.3s ease, height 0.3s ease',
            }}
          />
        </Box>
        <List sx={{ flexGrow: 1 }}>
          <ListItem button>
            <ListItemIcon sx={{ color: '#fff', minWidth: isOpen ? 40 : 24 }}>
              <HomeIcon />
            </ListItemIcon>
            {isOpen && (
              <ListItemText>
                <Link to="home" smooth={true} duration={500} offset={-100}>
                  Home
                </Link>
              </ListItemText>
            )}
          </ListItem>
          <ListItem button>
            <ListItemIcon sx={{ color: '#fff', minWidth: isOpen ? 40 : 24 }}>
              <MenuBookIcon />
            </ListItemIcon>
            {isOpen && (
              <ListItemText>
                <Link to="menu" smooth={true} duration={500} offset={-100}>
                  Menu
                </Link>
              </ListItemText>
            )}
          </ListItem>

          {isOpen && (
            <Grid
              container
              spacing={1}
              sx={{
                padding: '0 16px',
                display: 'grid',
                gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))',
                gap: '8px',
                overflowX: 'auto',
                cursor: 'grab',
              }}
              onMouseDown={(e) => {
                e.currentTarget.style.cursor = 'grabbing';
              }}
              onMouseUp={(e) => {
                e.currentTarget.style.cursor = 'grab';
              }}
            >
              {categories.map((category, index) => (
                <Grid item key={index} sx={{ overflow: 'hidden' }}>
                  <ListItem button>
                    <ListItemText
                      primary={
                        <Link to={formatCategory(category)} smooth={true} duration={500} offset={-100}>
                          {category}
                        </Link>
                      }
                      primaryTypographyProps={{
                        style: {
                          whiteSpace: 'nowrap',
                          overflow: 'hidden',
                          textOverflow: 'ellipsis',
                        },
                      }}
                    />
                  </ListItem>
                </Grid>
              ))}
            </Grid>
          )}
        </List>

        <List>
          <ListItem button>
            <ListItemIcon sx={{ color: '#fff', minWidth: isOpen ? 40 : 24 }}>
              <ContactMailIcon />
            </ListItemIcon>
            {isOpen && (
              <ListItemText>
                <Link to="contact" smooth={true} duration={500} offset={-100}>
                  Contact
                </Link>
              </ListItemText>
            )}
          </ListItem>
          <ListItem button>
            <ListItemIcon sx={{ color: '#fff', minWidth: isOpen ? 40 : 24 }}>
              <InfoIcon />
            </ListItemIcon>
            {isOpen && (
              <ListItemText>
                <Link to="about" smooth={true} duration={500} offset={-100}>
                  About Us
                </Link>
              </ListItemText>
            )}
          </ListItem>
        </List>
      </Drawer>
    </Box>
  );
};

export default Sidebar;
