import React, { useState, useEffect } from 'react';
import { Container, Grid, Card, CardContent, CardMedia, Typography } from '@mui/material';
import logo from '../assets/cropped_logo.png';

const Menu = () => {
  const [groupedMenuItems, setGroupedMenuItems] = useState({});

  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/data/menu_data.json`)
      .then((response) => {
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        return response.json();
      })
      .then((data) => {
        // 按类别分组
        const groupedData = data.reduce((acc, item) => {
          const category = item.category || 'Uncategorized';
          if (!acc[category]) {
            acc[category] = [];
          }
          acc[category].push(item);
          return acc;
        }, {});
        setGroupedMenuItems(groupedData);
      })
      .catch((error) => console.error('Error fetching menu data:', error));
  }, []);

  const formatCategory = (category) => category.replace(/\s+/g, '_').toLowerCase(); // 保证分类id格式一致

  return (
    <Container sx={{ marginTop: 4 }}>
      <Typography variant="h4" align="center" gutterBottom>
        Our Menu
      </Typography>
      {Object.keys(groupedMenuItems).length > 0 ? (
        Object.keys(groupedMenuItems).map((category, index) => (
          <div key={index} id={formatCategory(category)}> {/* 在这里使用 formatCategory 保证id匹配 */}
            <Typography variant="h5" align="left" gutterBottom sx={{ marginTop: 4 }}>
              {category}
            </Typography>
            <Grid container spacing={4}>
              {groupedMenuItems[category].map((item, idx) => (
                <Grid item xs={12} sm={6} md={4} key={idx}>
                  <Card sx={{ maxWidth: 345, margin: '20px auto' }}>
                    <CardMedia
                      component="img"
                      sx={{ height: 140 }}
                      image={item.image && item.image !== "No Image" ? `${process.env.PUBLIC_URL}${item.image}` : logo}// `${process.env.PUBLIC_URL}/assets/images/placeholder.png`} // 使用占位图片
                      alt={item.name}
                    />
                    <CardContent>
                      <Typography variant="h6">{item.name}</Typography>
                      <Typography variant="body2" color="textSecondary">
                        {item.description || "No description available"}
                      </Typography>
                      <Typography variant="subtitle1" color="primary">
                        {item.price}
                      </Typography>
                    </CardContent>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </div>
        ))
      ) : (
        <Typography variant="h6" align="center">
          No menu items available
        </Typography>
      )}
    </Container>
  );
};

export default Menu;
